<template>
    <div>
        <!-- 验证码容器 -->
        <components v-if="componentType"
                    :is="componentType"
                    :type="verifyType"
                    :figure="figure"
                    :arith="arith"
                    :width="width"
                    :height="height"
                    :fontSize="fontSize"
                    :codeLength="codeLength"
                    ref="instance"></components>
        <!-- 确定按钮容器 -->
        <div @click="checkCode" style="width:0; height:0;">
            <slot name="check">
                <button class="verify-btn">{{i18n('ok')}}</button>
            </slot>
        </div>
    </div>
</template>
<script type="text/babel">
    /**
     * Verify 验证码组件
     * @description 分发验证码使用
     * */
    import VerifyCode from './Verify/VerifyCode'
    import VerifySlide from './Verify/VerifySlide'

    export default {
        name: 'Vue2Verify',
        props: {
            // 双语化
            locale: {
                require: false,
                type: String,
                default() {
                    // 默认语言不输入为浏览器语言
                    if (navigator.language) {
                        var language = navigator.language;
                    }
                    else {
                        var language = navigator.browserLanguage;
                    }
                    return language
                }
            },
            type: {
                type: String | Number,
                require: false,
                default: 'picture'
            },
            figure: {
                type: Number
            },
            arith: {
                type: Number
            },
            width: {
                type: String
            },
            height: {
                type: String
            },
            fontSize: {
                type: String
            },
            codeLength: {
                type: Number
            },
            mode: {
                type: String
            },
            vOffset: {
                type: Number
            },
            vSpace: {
                type: Number
            },
            explain: {
                type: String
            },
            imgUrl: {
                type: String
            },
            imgName: {
                type: Array
            },
            imgSize: {
                type: Object
            },
            blockSize: {
                type: Object
            },
            barSize: {
                type: Object
            }
        },
        data() {
            return {
                // 内部类型
                verifyType: undefined,
                // 所用组件类型
                componentType: undefined
            }
        },
        methods: {
            /**
             * i18n
             * @description 兼容vue-i18n 调用$t来转换ok
             * @param {String} text-被转换的目标
             * @return {String} i18n的结果
             * */
            i18n(text) {
                if (this.$t) {
                    return this.$t(text)
                } else {
                    // 兼容不存在的语言
                    let i18n = this.$options.i18n.messages[this.locale] || this.$options.i18n.messages['en-US']
                    return i18n[text]
                }
            },
            /**
             * checkCode
             * @description 判断验证码
             * */
            checkCode() {
                if (this.instance.checkCode) {
                    this.instance.checkCode()
                }
            },
            /**
             * refresh
             * @description 刷新
             * */
            refresh() {
                if (this.instance.refresh) {
                    this.instance.refresh()
                }
            }

        },
        computed: {
            instance() {
                return this.$refs.instance || {}
            }
        },
        watch: {
            type: {
                immediate: true,
                handler(type) {
                    switch (type.toString()) {
                        case 'picture':
                            this.verifyType = '1'
                            this.componentType = 'VerifyCode'
                            break
                        case '1':
                            this.verifyType = '1'
                            this.componentType = 'VerifyCode'
                            break
                        case 'compute':
                            this.verifyType = '2'
                            this.componentType = 'VerifyCode'
                            break
                        case '2':
                            this.verifyType = '2'
                            this.componentType = 'VerifyCode'
                            break
                        case 'slide':
                            this.verifyType = '1'
                            this.componentType = 'VerifySlide'
                            break
                        case '3':
                            this.verifyType = '1'
                            this.componentType = 'VerifySlide'
                            break
                        case 'puzzle':
                            this.verifyType = '2'
                            this.componentType = 'VerifySlide'
                            break
                        case '4':
                            this.verifyType = '2'
                            this.componentType = 'VerifySlide'
                            break
                        default:
                            this.verifyType = undefined
                            this.componentType = undefined
                            console.error('Unsupported Type:' + type)
                    }
                }
            }
        },
        components: {
            VerifyCode,
            VerifySlide
        },
        i18n: {
            messages: {
                'en-US': {
                    ok: 'ok'
                },
                'zh-CN': {
                    ok: '确定'
                }
            }
        }
    }
</script>
<style>
    /*常规验证码*/
    .verify-code {
        font-size: 20px;
        text-align: center;
        cursor: pointer;
        margin-bottom: 5px;
        border: 1px solid #ddd;
    }

    .cerify-code-panel {
        height: 100%;
        overflow: hidden;
    }

    .verify-code-area {
        float: left;
    }

    .verify-input-area {
        float: left;
        width: 60%;
        padding-right: 10px;

    }

    .verify-change-area {
        line-height: 30px;
        float: left;
    }

    .varify-input-code {
        display: inline-block;
        width: 100%;
        height: 25px;
    }

    .verify-change-code {
        color: #337AB7;
        cursor: pointer;
    }

    .verify-btn {
        width: 200px;
        height: 30px;
        background-color: #337AB7;
        color: #FFFFFF;
        border: none;
        margin-top: 10px;
    }

    /*滑动验证码*/
    .verify-bar-area {
        position: relative;
        background: #FFFFFF;
        text-align: center;
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;
        border: 1px solid #ddd;
        -webkit-border-radius: 4px;
    }

    .verify-bar-area .verify-move-block {
        position: absolute;
        top: 0px;
        left: 0;
        background: #fff;
        cursor: pointer;
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;
        box-shadow: 0 0 2px #888888;
        -webkit-border-radius: 1px;
    }

    .verify-bar-area .verify-move-block:hover {
        background-color: #337ab7;
        color: #FFFFFF;
    }

    .verify-bar-area .verify-left-bar {
        position: absolute;
        top: -1px;
        left: -1px;
        background: #f0fff0;
        cursor: pointer;
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;
        border: 1px solid #ddd;
    }

    .verify-img-panel {
        margin: 0;
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;
        border: 1px solid #ddd;
        border-radius: 3px;
        position: relative;
    }

    .verify-img-panel .verify-refresh {
        width: 25px;
        height: 25px;
        text-align: center;
        padding: 5px;
        cursor: pointer;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 2;
    }

    .verify-img-panel .icon-refresh {
        font-size: 20px;
        color: #fff;
    }

    .verify-img-panel .verify-gap {
        background-color: #fff;
        position: relative;
        z-index: 2;
        border: 1px solid #fff;
    }

    .verify-bar-area .verify-move-block .verify-sub-block {
        position: absolute;
        text-align: center;
        z-index: 3;
        border: 1px solid #fff;
    }

    .verify-bar-area .verify-move-block .verify-icon {
        font-size: 18px;
    }

    .verify-bar-area .verify-msg {
        z-index: 3;
    }

    /*字体图标的css*/
    /*@font-face {font-family: "iconfont";*/
    /*src: url('../fonts/iconfont.eot?t=1508229193188'); !* IE9*!*/
    /*src: url('../fonts/iconfont.eot?t=1508229193188#iefix') format('embedded-opentype'), !* IE6-IE8 *!*/
    /*url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),*/
    /*url('../fonts/iconfont.ttf?t=1508229193188') format('truetype'), !* chrome, firefox, opera, Safari, Android, iOS 4.2+*!*/
    /*url('../fonts/iconfont.svg?t=1508229193188#iconfont') format('svg'); !* iOS 4.1- *!*/
    /*}*/

    .iconfont {
        font-family: "iconfont" !important;
        font-size: 16px;
        font-style: normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }

    .icon-check:before {
        content: "\e645";
    }

    .icon-close:before {
        content: "\e646";
    }

    .icon-right:before {
        content: "\e6a3";
    }

    .icon-refresh:before {
        content: "\e6a4";
    }
</style>


